import { ContainerModule, interfaces } from "inversify";
import { clientRTTI, clientProviderRTTI, clientOptionsRTTI } from "./client.rtti";
import { Client, ClientOptions, ClientProvider } from "~/modules/client";
import { ClientMock } from "./Client/ClientMock/Client.mock";
import { createClient } from "./ClientProvider/createClient";
import { ClientOptionsStore } from "./ClientOptions/ClientOptionsStore";
import { DeepPartial } from "~/utils/DeepPartial";

export const clientModuleContainerMock = new ContainerModule((bind: interfaces.Bind) => {
    bind<Client>(clientRTTI).to(ClientMock).inSingletonScope();
    bind<ClientProvider>(clientProviderRTTI).toProvider<Client>((context: interfaces.Context) => {
        return (token: string, userOptions?: DeepPartial<ClientOptions>) => {
            return createClient(context.container, token, userOptions);
        };
    });
    bind<ClientOptions>(clientOptionsRTTI).to(ClientOptionsStore).inSingletonScope();
});
