import { injectable } from "inversify";
import { TelemetryClient, TelemetryErrorEvent, TelemetryEvent, TelemetryEventGroup } from "~/modules/telemetry";
import { TelemetryProcessingResult } from "~/modules/telemetry/TelemetryProcessor/TelemetryProcessingResult";

@injectable()
export class TelemetryEventGroupMock<T> implements TelemetryEventGroup<T> {
    name?: string;

    telemetryClient: TelemetryClient<object>;

    events: TelemetryEvent[] = [];

    addEvents = jest.fn((...events: (T & TelemetryEvent)[]): Promise<TelemetryProcessingResult> => {
        this.events.push(...events);
        return Promise.resolve({
            eventsSucceeded: events.length,
            eventsFailed: 0,
            eventsNotProcessed: 0
        });
    });

    addErrorEvent = jest.fn((errorEvent: TelemetryErrorEvent) => {
        this.events.push(errorEvent);
        return Promise.resolve({
            eventsSucceeded: 1,
            eventsFailed: 0,
            eventsNotProcessed: 0
        });
    });

    $setName(name: string): void {
        this.name = name;
    }
}
