import { inject, injectable } from "inversify";
import { Twilsock, TwilsockResult, Headers } from "./Twilsock";
import { TwilsockEvent } from "./TwilsockEvent";
import { twilsockClientFactoryRTTI } from "~/modules/websocket/websocket.rtti";
import { Emitter, eventEmitterRTTI } from "~/modules/events";
import { ErrorCode, ErrorSeverity, FlexSdkError } from "~/modules/error";
import { TwilsockClientFactory } from "~/modules/websocket/TwilsockClientFactory/TwilsockClientFactory";
import { TwilsockClient } from "~/modules/websocket/TwilsockClient/TwilsockClient";
import { extractFileNameFromPath, extractModuleFromPath } from "~/utils/extractFromPath";

@injectable()
export class TwilsockMock implements Twilsock {
    readonly #twilsockClientFactory: TwilsockClientFactory;

    #twilsockClient: TwilsockClient;

    readonly #emitter: Emitter;

    constructor(
        @inject(twilsockClientFactoryRTTI) twilsockClientFactory: TwilsockClientFactory,
        @inject(eventEmitterRTTI) emitter: Emitter
    ) {
        this.#twilsockClientFactory = twilsockClientFactory;
        this.#emitter = emitter;
    }

    connect = jest.fn(() => {
        return Promise.resolve();
    });

    destroy = jest.fn(() => {
        this.$emit(TwilsockEvent.Disconnected);
        this.#emitter.removeAllListeners();
        return Promise.resolve();
    });

    updateToken = jest.fn(() => {
        this.$emit(TwilsockEvent.TokenUpdated);
        return Promise.resolve();
    });

    getRawTwilsockClient = jest.fn(() => {
        if (!this.#twilsockClient) {
            this.#twilsockClient = this.#twilsockClientFactory("token", "productID");
        }
        return this.#twilsockClient;
    });

    isConnected = jest.fn(() => {
        if (!this.#twilsockClient) {
            return false;
        }
        return this.#twilsockClient.isConnected;
    });

    $emit(event: TwilsockEvent, ...args: unknown[]): boolean {
        return this.#emitter.emit(event, ...args);
    }

    $setupErrorState(): void {
        const metadata = {
            module: extractModuleFromPath(__dirname),
            severity: ErrorSeverity.Error,
            source: extractFileNameFromPath(__filename)
        };
        this.connect.mockRejectedValue(new FlexSdkError(ErrorCode.SDK, metadata));
        this.post.mockRejectedValue(new FlexSdkError(ErrorCode.SDK, metadata));
        this.updateToken.mockRejectedValue(new FlexSdkError(ErrorCode.SDK, metadata));
    }

    
    post = jest.fn(function postMock<T = any>(url: string, headers: Headers, body: unknown) {
        return Promise.resolve({
            status: {
                code: 204,
                status: "No content"
            },
            headers: {}
        } as TwilsockResult<T>);
    });

    addListener(eventName: TwilsockEvent, listener: (...args: unknown[]) => void): this {
        this.#emitter.on(eventName, listener);
        return this;
    }

    removeListener(eventName: TwilsockEvent, listener: (...args: unknown[]) => void): this {
        this.#emitter.removeListener(eventName, listener);
        return this;
    }
}
