import { injectable } from "inversify";
import { ErrorCode, FlexSdkError } from "~/modules/error";
import { RateLimiter } from "~/modules/ratelimiter/RateLimiter/RateLimiter";
import { RateLimiterOptions } from "~/modules/ratelimiter/RateLimiter/RateLimiterOptions";

@injectable()
export class RateLimiterMock implements RateLimiter {
    maxHitsInTimeWindow: number;

    hitCountInTimeWindow: number = 0;

    timeWindow: number;

    $options: RateLimiterOptions;

    $mockedErrorCode: ErrorCode;

    check = jest.fn(() => {
        this.hitCountInTimeWindow += 1;

        if (this.$mockedErrorCode) {
            throw new FlexSdkError(this.$mockedErrorCode);
        }
    });

    cleanup = jest.fn();

    setup = jest.fn((options: RateLimiterOptions) => {
        this.$options = options;

        this.maxHitsInTimeWindow = options.maxHitsInTimeWindow;
        this.timeWindow = options.timeWindow;
    });

    $mockThrowsError(errorCode: ErrorCode) {
        this.$mockedErrorCode = errorCode;
    }

    $mockResetTimeWindow() {
        this.hitCountInTimeWindow = 0;
    }
}
