import { injectable, inject, optional } from "inversify";
import { Client, ClientConfigType, ClientEvent } from "~/modules/client";
import { Role } from "~/modules/auth";
import { Session, sessionRTTI } from "~/modules/session";
import { AccountConfig, accountConfigRTTI, FeaturesProvider, featuresProviderRTTI } from "~/modules/config";
import { ClientMockOptions } from "./ClientMockOptions";
import { clientMockOptionsRTTI } from "~/modules/client/mock";
import { Emitter, eventEmitterRTTI } from "~/modules/events";

@injectable()
export class ClientMock implements Client {
    readonly #session: Session;

    public readonly config: ClientConfigType;

    public roles: Array<Role> = [Role.Agent];

    public token: string = "custom token";

    readonly #emitter: Emitter;

    constructor(
        @inject(sessionRTTI) session: Session,
        @inject(accountConfigRTTI) accountConfig: AccountConfig,
        @inject(featuresProviderRTTI) fetchFeatures: FeaturesProvider,
        @inject(clientMockOptionsRTTI) @optional() mockOptions: ClientMockOptions,
        @inject(eventEmitterRTTI) emitter: Emitter
    ) {
        this.#session = session;
        const account = mockOptions?.accountConfig || accountConfig;
        this.config = {
            account,
            fetchFeatures
        };
        this.#emitter = emitter;
    }

    init = jest.fn((token: string) => {
        this.token = token;
        return Promise.resolve();
    });

    updateToken = jest.fn(async (token: string) => {
        await this.#session.updateToken(token);
        this.token = token;
    });

    destroy = jest.fn();

    createTelemetryClient = jest.fn();

    addListener(eventName: ClientEvent, listener: (...args: unknown[]) => void): this {
        this.#emitter.on(eventName, listener);
        return this;
    }

    removeListener(eventName: ClientEvent, listener: (...args: unknown[]) => void): this {
        this.#emitter.removeListener(eventName, listener);
        return this;
    }
}
