import { injectable, inject } from "inversify";
import { TelemetryEvent } from "~/modules/telemetry";
import { TelemetryProcessor } from "~/modules/telemetry/TelemetryProcessor/TelemetryProcessor";
import { Logger, loggerFactoryRTTI, LoggerFactory, TelemetryLoggerName } from "~/modules/logger";

@injectable()
export class TelemetryProcessorMock implements TelemetryProcessor {
    readonly #logger: Logger;

    constructor(@inject(loggerFactoryRTTI) getLogger: LoggerFactory<TelemetryLoggerName>) {
        this.#logger = getLogger(TelemetryLoggerName.TelemetryProcessor);
    }

    processEvents = jest.fn(
        (groupName: string, payloadType: string, sessionData?: object, ...events: TelemetryEvent[]) => {
            this.#logger.debug("common attributes: ", sessionData);
            events.forEach((event) =>
                this.#logger.log("sending event for group: ", groupName, "payload type", payloadType, event.eventName)
            );
            return Promise.resolve({
                eventsSucceeded: events.length,
                eventsFailed: 0,
                eventsNotProcessed: 0
            });
        }
    );
}
