import { injectable, inject } from "inversify";
import { Session, SessionEvent } from "~/modules/session";
import { Authenticator, authenticatorRTTI, Role } from "~/modules/auth";
import { Twilsock, twilsockRTTI } from "~/modules/websocket";
import { Emitter, eventEmitterRTTI } from "~/modules/events";

@injectable()
export class SessionMock implements Session {
    public token: string = "token";

    readonly #auth: Authenticator;

    readonly #connection: Twilsock;

    public roles: Array<Role> = [];

    readonly #emitter: Emitter;

    constructor(
        @inject(authenticatorRTTI) auth: Authenticator,
        @inject(twilsockRTTI) connection: Twilsock,
        @inject(eventEmitterRTTI) emitter: Emitter
    ) {
        this.#auth = auth;
        this.#connection = connection;
        this.#emitter = emitter;
    }

    init = jest.fn((token: string) => {
        this.token = token;
        return Promise.resolve();
    });

    updateToken = jest.fn(() => {
        return Promise.resolve();
    });

    destroy = jest.fn(() => {
        this.#emitter.removeAllListeners();
        return Promise.resolve();
    });

    $emit(event: SessionEvent, ...args: unknown[]): boolean {
        return this.#emitter.emit(event, ...args);
    }

    addListener(eventName: SessionEvent, listener: (...args: unknown[]) => void): this {
        this.#emitter.on(eventName, listener);
        return this;
    }

    removeListener(eventName: SessionEvent, listener: (...args: unknown[]) => void): this {
        this.#emitter.removeListener(eventName, listener);
        return this;
    }
}
