import { ContainerModule, interfaces } from "inversify";
import { newableTwilsockClientRTTI, twilsockRTTI, productIdRTTI, twilsockClientFactoryRTTI } from "./websocket.rtti";
import { Twilsock } from "./Twilsock/Twilsock";
import { TwilsockMock } from "./Twilsock/Twilsock.mock";
import { NewableTwilsockClient } from "./TwilsockClient/TwilsockClient";
import { TwilsockClientMock } from "./TwilsockClient/TwilsockClient.mock";
import { TwilsockClientFactory } from "~/modules/websocket/TwilsockClientFactory/TwilsockClientFactory";

export const websocketModuleContainerMock = new ContainerModule((bind: interfaces.Bind) => {
    let twilsockClient: TwilsockClientMock;
    bind<NewableTwilsockClient>(newableTwilsockClientRTTI).toConstructor(TwilsockClientMock);
    bind<string>(productIdRTTI).toConstantValue("TEST_PRODUCT");
    bind<Twilsock>(twilsockRTTI).to(TwilsockMock).inSingletonScope();
    bind<TwilsockClientFactory>(twilsockClientFactoryRTTI).toFactory<TwilsockClientMock>(() => () => {
        if (!twilsockClient) {
            twilsockClient = new TwilsockClientMock();
        }
        return twilsockClient;
    });
});
