import { injectable } from "inversify";
import { AccountConfigKeys, AccountConfigData, AccountConfig } from "~/modules/config";

import { accountConfigDataMock } from "./AccountConfigData.mock";

@injectable()
export class AccountConfigMock implements AccountConfig {
    #accountConfigData: AccountConfigData = accountConfigDataMock;

    get(): AccountConfigData {
        return this.#accountConfigData;
    }

    set(values: Partial<AccountConfigData>): Promise<void> {
        this.#accountConfigData = { ...this.#accountConfigData, ...values };
        return Promise.resolve();
    }

    $set<K extends AccountConfigKeys>(key: K, value: AccountConfigData[K]): void {
        this.$setAccountConfigData({ [key]: value });
    }

    $setAccountConfigData(accountConfigData: Partial<AccountConfigData>): void {
        this.#accountConfigData = { ...this.#accountConfigData, ...accountConfigData };
    }
}
