import { ContainerModule, interfaces } from "inversify";
import { ClearCache } from "~/modules/storage/ClearCache/ClearCache";
import { clearLocalStorageMock } from "~/modules/storage/ClearCache/clearLocalStorageMock";
import { LocalStorage } from "./LocalStorage/LocalStorage";
import { LocalStorageMock } from "./LocalStorage/LocalStorageMock";
import { clearCacheRTTI, localStorageRTTI } from "./storage.rtti";

export const storageModuleContainerMock = new ContainerModule((bind: interfaces.Bind) => {
    bind<LocalStorage>(localStorageRTTI).to(LocalStorageMock).inSingletonScope();
    bind<ClearCache>(clearCacheRTTI).toConstantValue(clearLocalStorageMock);
});
