import { Emitter } from "~/modules/events";
import { TwilsockReplyError } from "~/modules/websocket/Twilsock/TwilsockReplyError";
import { TwilsockClient, TwilsockClientEvent } from "~/modules/websocket/TwilsockClient/TwilsockClient";

export class TwilsockClientMock extends Emitter implements TwilsockClient {
    isConnected = false;

    connect = jest.fn();

    disconnect = jest.fn(() => {
        this.emit(TwilsockClientEvent.Disconnected);
        return Promise.resolve();
    });

    updateToken = jest.fn(() => Promise.resolve());

    post = jest.fn(() => Promise.resolve({ status: { code: 123, status: "status" }, headers: {} }));

    $getRateLimitError = () => {
        const postError = new TwilsockReplyError("Rate limit exceeded error");
        Object.assign(postError, {
            reply: {
                status: {
                    code: 429
                }
            },
            body: {
                code: 20429,
                message: "Rate limit exceeded"
            }
        });
        return postError;
    };

    on = (event: TwilsockClientEvent, listener: (...args: unknown[]) => void) => {
        super.on(event, listener);
        return this;
    };

    removeListener = (eventName: TwilsockClientEvent, listener: (...args: unknown[]) => void) => {
        super.removeListener(eventName, listener);
        return this;
    };

    emit = (event: string, ...args: unknown[]) => {
        return super.emit(event, ...args);
    };
}
