import { injectable, inject } from "inversify";
import { TelemetryEventGroup, TelemetryClient } from "~/modules/telemetry";
import { TelemetryEventGroupFactory } from "~/modules/telemetry/TelemetryEventGroupFactory/TelemetryEventGroupFactory";
import { telemetryEventGroupFactoryRTTI } from "~/modules/telemetry/telemetry.rtti";

@injectable()
export class TelemetryClientMock<U extends object> implements TelemetryClient<U> {
    name: string;

    sessionData?: U;

    readonly #eventGroupFactory: TelemetryEventGroupFactory<unknown>;

    constructor(@inject(telemetryEventGroupFactoryRTTI) eventGroupFactory: TelemetryEventGroupFactory<U>) {
        this.#eventGroupFactory = eventGroupFactory;
    }

    createEventGroup = jest.fn(<T>(name?: string): TelemetryEventGroup<T> => {
        const eventGroup = this.#eventGroupFactory(this, name);
        return eventGroup;
    });

    setSessionData = jest.fn((sessionData: U): Promise<void> => {
        this.sessionData = sessionData;
        return Promise.resolve();
    });
}
