import { injectable } from "inversify";
import { LocalStorage } from "./LocalStorage";
import { LocalStorageKeys } from "./LocalStorageKeys";

let items: { [key: string]: unknown } = {};

export const dropLocalStorage = () => {
    items = {};
};

@injectable()
export class LocalStorageMock implements LocalStorage {
    getCachedItem<T = unknown>(key: LocalStorageKeys): undefined | T {
        const item = items[key];

        if (typeof item === "undefined") {
            return undefined;
        }

        return items[key] as T;
    }

    setCachedItem(key: string, value: unknown) {
        items[key] = value;
    }
}
